import React, { useState, useEffect } from "react";
import { useSearchParams, Link } from "react-router-dom";
import NavigationBar from "../components/NavigationBar";
import "./Search.css";
import axios from "axios";

function Search() {
  const [searchParams, setSearchParams] = useSearchParams();

  const querySearchKeyword = searchParams.get("s") || "";

  const [APIData, setAPIData] = useState([]);

  useEffect(() => {
    axios
      .get(`https://gogoanime.herokuapp.com/search?keyw=${querySearchKeyword}`)
      .then((response) => {
        setAPIData(response.data);
      });
  }, [querySearchKeyword]);

  function handleKeypress(e) {
    if (e.key === "Enter") {
      setSearchParams({
        s: e.target.value
      });
    }
  }

  return (
    <div>
      <NavigationBar />
      <div id="search">
        <input onKeyPress={handleKeypress} defaultValue={querySearchKeyword} />
        <div id="entries-container">
          {APIData.map((entry) => {
            return (
              <div key={entry.animeId}>
                <Link to={`/anime/${entry.animeId}`}>{entry.animeTitle}</Link>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}

export default Search;
