import { useEffect, useRef } from "react";
import Hls from "hls.js";
import Plyr from "plyr-react";

const MyComponent = (sources) => {
  const ref = useRef(null);
  useEffect(() => {
    const loadVideo = async () => {
      const video = document.getElementById("plyr");
      var hls = new Hls(/*{
        xhrSetup: function (xhr) {
          xhr.setRequestHeader(
            "referer",
            "https://goload.pro/streaming.php?id=MjUwNTQ=&title=Naruto+Episode+1&typesub=SUB"
          );
        }
      }*/);
      hls.loadSource(
        sources.sources.sources_bk[0].file
        //"https://www05.gogocdn.stream/videos/hls/WtHCysuScbUe4OBgGoDWbw/1650252480/25054/027e9529af2b06fe7b4f47e507a787eb/ep.1.1645179467.m3u8"
      );
      hls.attachMedia(video);
      // @ts-ignore
      ref.current.plyr.media = video;

      hls.on(Hls.Events.MANIFEST_PARSED, function () {
        ref.current.plyr.play();
      });
    };
    loadVideo();
  });

  return <Plyr id="plyr" source={{}} ref={ref} />;
};

export default function AnimePlayer(sources) {
  const supported = Hls.isSupported();

  return (
    <div>
      {supported ? (
        <MyComponent sources={sources.sources} />
      ) : (
        "HLS is not supported in your browser"
      )}
    </div>
  );
}
